.button {
  display: inline-block;
  margin: 11px 0;

  padding: 11px 20px;

  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  color: $color-bg-primary;

  background-color: $color-base-white;
  border: none;
  cursor: pointer;

  transition: opacity $trans-default;

  &--red {
    background-color: $color-torch-red;
  }

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 0.8;
    }
  }
}
